@import "../../../assets/styles/mixin";

.span {
  // font-family: "Nanum";
  color: white;
  font-size: 1.2rem;
}

.survey__btn {
  @include bluebtn;
  width: fit-content;
  &:hover {
    background-color: #51aabf;
  }
}
.survey__btn__post {
  @include bluebtn;
  width: fit-content;
  &:hover {
    height: 40px;
  }
}

.survey__btn__block {
  top: 18rem;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
}

.survey__btn__active {
  @include bluebtn;
  width: fit-content;
  background-color: #202363;
}
.survey__textarea {
  border: 1px solid white;
  font-size: 2rem;
  resize: none;
  text-align: center;
  display: flex;
  align-items: center;
  padding: 5% 0;
}
