@font-face {
  font-family: "IM_Hyemin-Regular";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2106@1.1/IM_Hyemin-Regular.woff2")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

.surveySp {
  & * {
    box-sizing: border-box;
    font-family: "IM_Hyemin-Regular";
  }
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  vertical-align: middle;
  align-content: center;
  text-align: center;
  max-width: 800px;
  width: 100vw;
  height: 100vh;
  background-color: beige;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow: hidden;
}

.progressBar_content {
  position: relative;
  max-width: 800px;
  height: 30vh;
}

.progressBarText {
  position: absolute;
  top: 20%;
  right: 0;
  left: 0;
}

.bar {
  position: relative;
  top: 50%;
  width: 90%;
  margin: 0 auto;
}

.barAll {
  border: 1px solid gray;
  border-radius: 1em;
  width: 90%;
  height: 20%;
  color: gray;
  margin: 0 auto;
}

.survey__section {
  height: 100vh;
  padding: 10% 0;
  display: flex;
  flex-direction: column;
  & .survey__result {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  & .survey__comments {
    overflow-y: auto;
    .survey__comments__wrapper {
      padding: 0 20px;
      margin: 30px 0;
      overflow-y: auto;
      .nick {
      }
      .comments {
        margin: 30px 0;
        font-size: 0.7rem;
        // margin-bottom: 100px;
      }
    }
    & button {
      padding: 10px 15px;
      margin: 0 20px;
      font-size: 20px;
      width: fit-content;
      border: 2px solid black;
      background-color: white;
      cursor: pointer;
    }
  }
}

@media screen and (max-width: 420px) {
  .surveySp {
    padding: 8% 0;
  }
  .survey__section {
    & .survey__result {
    }
    & .survey__comments {
      margin-top: 45vh;
      .survey__comments__wrapper {
        .nick {
        }
        .comments {
        }
      }
      & button {
        padding: 5px 10px;
        margin: 10px 20px;
      }
    }
  }
}
