.home__container {
  width: 100vw;
  background-color: #363636;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 10vw;
}

.home__section {
  width: 100%;
  height: 100vh;
  .home__section__wrapper {
    width: 100%;
    height: 90%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    .home__img {
      width: 100vw;
      height: 100vh;
      position: absolute;
      z-index: -1;
      filter: blur(50px);
    }
  }
}
.home__section__2 {
  .section__page2 {
    width: 100%;
    height: 100%;
    position: relative;
  }
  & .disable {
    position: absolute;
    top: 50%;
    width: 100%;
    left: -30%;
    transform: translate(-120%, 0%);
    transition: all 0.8s ease-in;
  }
  & .active {
    transition: all 0.8s ease-in;
    transform: translate(0%, 0%);
  }
  .title {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100vh;
    text-align: right;
    display: flex;
    flex-direction: column;
    justify-content: center;
    line-height: 3rem;
    z-index: 2;
    & pre {
      font-size: 200%;
      color: white;
      margin-bottom: 3%;
    }
    & span {
      color: white;
    }
  }
}
.home__section__2.active {
  visibility: visible;
}

.page4__img {
  transform: translate(-100%, 0%);
  transition: all 1s ease-in-out;
}

.page4__img__active {
  transition: all 1s ease-in-out;
  transform: translate(-15%, 0%);
}

.page5__img {
  transform: translate(100%, 0%);
  transition: all 1s ease-in-out;
}

.page5__img__active {
  transition: all 1s ease-in-out;
  transform: translate(-25%, 0%);
}

.page6__img {
  transition: all 1s ease-in-out;
}

.page6__img__active {
  transition: all 1s ease-in-out;
}

@keyframes page2 {
  from {
    transform: translate(-120%, 0%);
  }
  to {
    transform: translate(0%, 0%);
  }
}